.hero-gallery {
  margin-bottom: var(--margin-desk);

  @include tablet {
    margin-bottom: var(--margin-tablet);
  }
}

.hero-swiper {

  .swiper-button-prev,
  .swiper-button-next {
    width: 20px;
    height: 40px;
    opacity: 0.5;
    color: #fff;
  }

  .swiper-slide {
    position: relative;
    font-size: 18px;
    background: #fff;
    display: flex;
    flex-wrap: wrap;
    align-items: flex-end;
    min-height: 350px;
  }

  &__img {
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    position: absolute;
    object-fit: cover;
  }

  &__date {
    font-family: var(--font-family-Montserrat);
    font-weight: 200;
    font-style: italic;
    color: var(--white-text);
    opacity: 0.4;
  }

  &__text {
    font-family: var(--font-family-Montserrat);
    font-size: 20px;
    margin: 0;
    width: 95%;
    color: var(--white-text);
    opacity: 0.95;
    cursor: pointer;

    @include mobile {
      width: 100%;
    }
  }

  &__link {
    color: var(--white-text);
    cursor: pointer;
    position: relative;
    display: inline-block;
    overflow: hidden;
    font-size: 18px;
    font-weight: 500;

    span {
      position: absolute;
      left: 0;
      display: block;
      overflow: hidden;
      width: 0;
      height: 100%;
      font-size: 18px;
      font-weight: 700;
      backface-visibility: hidden;
      background-color: transparent;
      transition: width 0.2s;

      &::before {
        content: attr(data-text);
        position: absolute;
        right: 0;
        display: block;
        width: 100%;
        color: var(--red-bil);
        backface-visibility: hidden;
      }
    }

    &:hover span {
      width: 100%;
      transition: width 0.2s;
    }

  }

  &__descr {
    width: 100%;
    text-align: left;
    margin-bottom: 30px;
    // padding-top: 290px;
    // padding-bottom: 40px;
    padding-left: 10px;

    @include mobile {
      padding-left: 3px;
    }
  }
}