.breadcrumb {
  @include flex;

  li {
    position: relative;
    padding: 0;
    margin: 0;

    &:not(:last-child)::after {
      position: absolute;
      content: '>>';
      top: 2px;
      right: 13px;
      width: 8px;
      font-size: 15px;
      color: var(--text-grey);
    }

    &:first-child {
      margin-left: 7.5%;

      @include tablet {
        margin-left: 0;
      }
    }
  }

  &__link {
    color: var(--text-grey);
    display: inline;
    font-size: 12px;
    padding: 0px 26px 0px 0px;

    &_active {
      color: var(--text);
    }

  }
}