.tag {
  width: 150px;
  height: 10px;
  position: absolute;
  top: 5px;
  left: 20px;
  font-size: 10px;
  color: var(--text-grey);

  @include big-desktop {
    left: 50%;
    transform: translateX(-50%);
  }

}

.tag-big-news {
  width: 150px;
  height: 10px;
  position: absolute;
  top: 10px;
  left: 20px;
  font-size: 10px;
  color: var(--text-grey);

  @include mobile {
    left: 0;
  }

}


.tag-alboms {
  width: 150px;
  height: 10px;
  position: absolute;
  top: -10px;
  left: 0px;
  font-size: 10px;
  color: var(--text-grey);

}

.tag-cat {

  font-size: 10px;
  color: var(--text-grey);
}