.partners {
  margin-bottom: var(--margin-desk);

  @include tablet {
    margin-bottom: var(--margin-tablet);
  }

  &__container {
    padding: 0 var(--container-offset-sec);
    max-width: var(--container-width-sec);
  }

  &__list {
    display: flex;
    justify-content: space-around;
    align-items: center;
  }

  &__link {
    display: block;
  }

  &__item {
    width: 15%;

    @include mobile {
      width: 22%;
    }
  }
}