.tabs {
  display: flex;
  justify-content: space-between;

  @include tablet {
    flex-direction: column;
  }

  &__panel {
    margin-top: -68px;
    display: none;

    @include tablet {
      margin-top: 0;
    }

    &--active {
      display: block;
    }
  }

  &__content {
    width: 80%;

    @include tablet {
      width: 100%;
    }
  }

  &__nav {
    display: flex;
    flex-direction: column;
    justify-content: stretch;
    min-width: 10%;
    max-width: 14%;
    align-self: center;

    @include tablet {
      flex-direction: row;
      justify-content: center;
    }

    @include small-tablet {
      max-width: 70%;
    }

    @include mobile {
      flex-direction: column;
    }

    &-item {
      width: 100%;
      position: relative;
      display: block;
      flex-grow: 1;
      max-height: 80px;

      &:not(:last-child) {
        margin-bottom: -1px;

        @include tablet {
          margin-bottom: 0px;
        }
      }

      @include tablet {
        width: auto;
      }
    }

    &-btn {
      border: 1px solid var(--text);
      width: 100%;
      height: 100%;
      padding: 10px 20px;
      transition: background-color 0.3s ease-in-out;

      @include small-tablet {
        padding: 10px 6px;
      }

      &:hover {
        transition: background-color 0.3s ease-in-out;
        background-color: var(--main-color);
      }

      input {
        cursor: pointer;
      }
    }

    &-btn--active {
      background-color: var(--main-color);
    }
  }
}