.line-add {
  &::after {
    position: absolute;
    content: '';
    width: 65%;
    height: 1px;
    top: 50%;
    left: 230px;
    background-color: black;

    @include mobile {
      width: 45%;
      left: 185px;
    }

  }
}

.box__title.line-add {
  &::after {
    position: absolute;
    content: '';
    width: 80%;
    height: 1px;
    top: 50%;
    left: 280px;
    background-color: black;

    @include small-tablet {
      width: 57%;
      left: 227px;
    }

    @include mobile {
      width: 35%;
    }

  }
}