.online {
  margin-bottom: var(--margin-desk);

  &__box {
    width: 100%;
  }
}

.results {
  position: relative;

  &__info {
    display: block;
    margin: 0 auto;
    width: 50%;
    position: relative;
    text-align: center;
    padding: 25px 0px 5px 0px;

    @include small-tablet {
      width: 80%;
    }

    @include mobile {
      width: 90%;
    }
  }

  &__interactive {
    position: relative;
    height: 600px;

    @include small-tablet {
      height: 400px;
    }
  }

  &__info-team {
    display: inline-block;
    width: 34%;
    font-size: 15px;

    @include small-tablet {
      font-size: 12px;
      display: inline;
    }

    &.aurora {
      &.home {
        &::before {
          position: absolute;
          content: '';
          top: 8px;
          left: -4px;
          width: 40px;
          height: 40px;
          background-image: url('../img/teams/aurora.png');
          background-repeat: no-repeat;
          background-size: cover;
        }
      }
    }

    &.shaldunk {
      &.home {
        &::before {
          position: absolute;
          content: '';
          top: 8px;
          left: -4px;
          width: 35px;
          height: 35px;
          background-image: url('../img/teams/shaldunk.png');
          background-repeat: no-repeat;
          background-size: cover;
        }
      }
    }

    &.metrostroy {
      &.home {
        &::before {
          position: absolute;
          content: '';
          top: 6px;
          left: -6px;
          width: 45px;
          height: 45px;
          background-image: url('../img/teams/metrostroy.png');
          background-repeat: no-repeat;
          background-size: cover;

        }
      }
    }

    &.konstanta {
      &.home {
        &::before {
          position: absolute;
          content: '';
          top: 4px;
          left: -6px;
          width: 45px;
          height: 45px;
          background-image: url('../img/teams/konstanta.png');
          background-repeat: no-repeat;
          background-size: cover;
        }
      }
    }

    &.gazpromneft {
      &.home {
        &::before {
          position: absolute;
          content: '';
          top: 4px;
          left: -6px;
          width: 45px;
          height: 45px;
          background-image: url('../img/teams/neftgazprom.png');
          background-repeat: no-repeat;
          background-size: cover;
        }
      }
    }

    &.olimp {
