          background-size: cover;
        }
      }
    }
  }



  &__info-round {
    text-align: center;
    display: block;
    margin: 0;
    margin-top: 5px;
    font-size: 12px;
    color: var(--text-grey);
  }
}

.box {
  &__title {
    position: relative;
    margin: 0;
    margin-bottom: var(--margin-big-desktop);
    font-weight: 600;
    font-size: 28px;

    @include tablet {
      margin-bottom: var(--margin-tablet);
    }

  }
}