.contacts {
  background-color: var(--white-text);
  box-shadow: var(--text, 0.5) 0px 4px 5px 0px;
  margin-bottom: var(--margin-big-desktop);

  &__title {
    text-align: center;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
  }
  &__item {
    width: 50%;
    margin-bottom: 40px;
    display: flex;
    flex-direction: row;
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    margin-left: 20px;
  }

  &__email-link {
    font-weight: 500;
    color: var(--text);
    transition: color 0.3s;

    &:hover {
      transition: color 0.3s;
      color: var(--main-color);
    }
  }

  &__name {
    margin: 0;
    padding-bottom: 10px;
  }

  &__position {
    margin: 0;
  }

  &__email {
    margin: 0;
  }

  &__text {
    margin-top: 10px;
    margin-bottom: 10px;
    color: var(--text);
    display: block;
    text-align: center;

    &:last-child {
      padding-bottom: var(--margin-big-desktop);
    }

    & > a {
      color: var(--text);
    }

    & > a > i {
      transition: color 0.3s;

      &:hover {
        transition: color 0.3s;
        color: var(--main-color);
      }
    }
  }
}
