.big-news {
  margin-bottom: var(--margin-desk);

  @include tablet {
    margin-bottom: var(--margin-tablet);
  }

  &__container {
    padding: 0 var(--container-offset-sec);
    max-width: var(--container-width-sec);
    display: flex;
    gap: 28px;

    @include tablet {
      flex-direction: column;
    }
  }

  &__main {
    margin-top: 13px;
    width: 50%;
    border-top: 2px solid #000;

    @include tablet {
      width: 100%;
    }
  }

  &__side {
    width: 50%;

    @include tablet {
      width: 100%;
    }
  }

  &__text {
    margin: 0;
    font-size: 16px;
    line-height: 23px;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 6;
    -webkit-box-orient: vertical;
  }

  &__img {
    min-height: 430px;
    margin-top: 20px;
    margin-bottom: 51px;


    @include mobile {
      margin-bottom: 20px;
    }

    &_short {
      width: 100%;
      min-height: 230px;
      max-height: 323px;
    }
  }

  &__item {

    &:not(:last-child) {
      margin-bottom: 32px;

      @include mobile {
        margin-bottom: 5px;
      }
    }

  }

  &__title {
    position: relative;
    text-transform: uppercase;
    padding-top: 4px;
    padding-bottom: 9px;
    font-weight: 400;
    font-size: 32px;
    border-bottom: 1px solid #000;
  }

  &__title,
  &__newstitle {
    margin: 0;
    text-align: center;
  }

  &__newstitle {
    position: relative;
    display: inline-block;
    display: block;
    line-height: 35px;
    font-weight: 700;
    font-size: 28px;
    margin-bottom: 35px;

    @include mobile {
      font-size: 17px;
      line-height: 27px;
      margin-bottom: 15px;
    }

    &::before {
      top: -20px;
      left: 50%;
      transform: translateX(-50%);
      position: absolute;
      content: '';
      font-size: 10px;
    }
  }


  &__continue {
    padding-right: 18px;
    transition: color 0.3s;

    &:hover .move-right {
      transition: all 0.2s ease;
      animation-name: move-reverse;
      animation-direction: alternate;
      animation-duration: 0.4s;
      animation-iteration-count: infinite;

    }

    &:hover {
      transition: color 0.3s;
      color: var(--main-color);
    }
  }

  &__arrow {
    position: absolute;
    right: 2px;
  }

  &__continue,
  &__time {
    font-size: 11px;
    color: var(--text);
    opacity: 0.8;

    &:focus {
      outline: 2px solid var(--main-color);
      outline-offset: 2px;
    }

  }

  &__infopanel {
    position: relative;
    margin-top: 32px;
    padding-bottom: 26px;
    @include flex-all-sb;
  }
}