.nav {

  &__item {
    position: relative;


    &:not(:last-child) {
      margin-right: 38px;

      @include tablet {
        margin-right: 0;
      }

      &:before {
        content: '';
        top: 6px;
        right: -20px;
        width: 2px;
        height: 11px;
        position: absolute;
        border-right: 1px solid #000;

        @include tablet {
          width: 0;
          border: none;
        }
      }
    }

    &--footer {
      position: relative;
      margin-right: 0px;
      text-align: left;

      @include small-tablet {
        display: inline;
      }

      @include mobile {
        margin-right: 0px;

      }

      &:not(:last-child) {
        padding-bottom: 8px;
      }

      &-active {
        &:after {
          background: url('../img/icon-string.svg');
          background-size: 90%;
          background-repeat: no-repeat;
          position: absolute;
          content: "";
          height: 17px;
          width: 10px;
          top: 2px;
          left: -20px;
        }
      }

      &.nav__item {
        @include small-tablet {
          margin-right: 0;
        }

        @include mobile {
          text-align: center;
        }
      }
    }
  }

  .header--news__nav &__list {
    @include mobile {
      height: auto;
    }
  }

  &__list {
    width: 66%;
    padding-top: 5px;
    padding-bottom: 5px;
    flex-wrap: wrap;
    display: flex;
    justify-content: center;
    border-bottom: 1px solid #000;
    border-top: 2px solid #000;

    @include tablet {
      border-top: 2px solid #000;
      width: 100%;
      flex-direction: column;
      justify-content: space-around;
      align-items: center;
    }

    @include mobile {
      height: 70vh;
    }
  }

  &__link {
    font-family: "Exo2";
    font-style: normal;
    font-weight: 500;
    font-size: 13px;
    line-height: 19px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    transition: color 0.3s;
    color: var(--text);
    display: inline-block;
    text-decoration: none;
    cursor: pointer;

    &.active {
      transition: color 0.3s;
      color: var(--main-color);
    }

    @include tablet {
      font-size: 45px;
    }

    @include mobile {
      font-size: 30px;
    }

    &:after {
      background-color: var(--red-bil);
      display: block;
      content: "";
      height: 2px;
      width: 0%;
      -webkit-transition: width .3s ease-in-out;
      -moz--transition: width .3s ease-in-out;
      transition: width .3s ease-in-out;
    }

    &:hover:after,
    &:focus:after {
      width: 100%;
    }

    &:hover {
      transition: color 0.3s;
      color: var(--main-color);
    }

    &.active.nav__link--footer {
      transition: color 0.3s;
      color: var(--red-bil);
    }

    &--footer {
      display: inline-block;
      transition: color 0.3s;
      color: var(--white-text);
      font-weight: 400;
      text-transform: uppercase;

      &:after {
        background-color: var(--red-bil);
        display: block;
        content: "";
        height: 2px;
        width: 0%;
        -webkit-transition: width .3s ease-in-out;
        -moz--transition: width .3s ease-in-out;
        transition: width .3s ease-in-out;
      }

      &:hover:after,
      &:focus:after {
        width: 100%;
      }

      &.active {
        transition: color 0.3s;
        color: var(--main-color);
      }

      &:hover {
        transition: color 0.3s;
        color: var(--main-color);
      }
    }
  }
}