.header {

  &__container {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin-bottom: var(--margin-desk);

    @include tablet {
      flex-direction: row;
      justify-content: space-between;
      align-items: flex-start;
      margin-bottom: var(--margin-tablet);
    }

    @include tablet {
      justify-content: space-between;
    }

    @include small-tablet {
      justify-content: space-between;
    }
  }

  &__logo {
    display: block;
    align-items: center;

    @include for-desktop {
      order: -1;
    }

    @include big-desktop {
      order: -1;
    }

    @include tablet {
      width: 10%;
      order: 1;
    }

    @include mobile {
      align-self: center;
      width: 11.4%;
    }
  }

  &__title {
    position: relative;

    @include tablet {
      width: 78%;
      text-align: center;
    }

    @include mobile {
      width: 74%;
    }

    &::before,
    &::after {
      position: absolute;
      content: "";
      display: inline-block;
      vertical-align: middle;
      width: 100%;
      height: 2px;
      background-color: var(--grey-line);

    }

    &::before {
      top: 24%;
      margin-left: -100%;
      left: -14px;

      @include tablet {
        top: 92%;
        width: 140%;
      }

      @include small-tablet {
        width: 135%;
      }

      @include mobile {
        width: 128%;
      }
    }

    &::after {
      top: 24%;
      margin-right: -100%;
      right: -14px;

      @include tablet {
        top: 92%;
        width: 140%;
      }

      @include small-tablet {
        width: 135%;
      }

      @include mobile {
        width: 128%;
      }
    }
  }

  &__nav {
    width: 100%;
    padding: 10px 0px 12px 0px;
    display: flex;
    justify-content: center;
    max-width: 1201.3px;

    @include big-desktop {
      width: 99.8%;
    }
  }

  &__text {
    display: inline-block;
    vertical-align: middle;
    padding: 0;
    margin: 0 auto;
    margin-bottom: 17px;
    text-align: center;
    text-transform: uppercase;
    font-weight: 400;
    font-size: 45px;
    color: var(--main-color);

    @include big-desktop {
      font-size: 45px;
    }

    @include tablet {
      margin-top: 18px;
      margin-bottom: 14px;
      font-size: 35px;
    }

    @include small-tablet {
      font-size: 22px;
    }

    @include mobile {
      font-size: 17px;
    }

    &_lowcase {
      text-transform: initial;
    }
  }

  &__time {
    width: 85.2%;
    display: block;
    margin: 0 auto;
    margin-bottom: 35px;
    text-align: center;
    font-weight: 500;
    font-size: 11px;

    @include tablet {
      width: 62%;
      margin-bottom: 0;
    }
  }
}

.burger {
  position: relative;
  display: none;
  width: 30px;
  height: 20px;
  z-index: 11;

  @include tablet {
    width: 45px;
    height: 30px;
  }

  @include tablet {
    width: 40px;
  }

  &__line {
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    display: block;
    background-color: var(--text);
    height: 3px;
    width: 100%;
    transition: opacity 0.3s ease-in-out;
  }

  &::after,
  &::before {
    content: "";
    position: absolute;
    left: 0;
    height: 3px;
    width: 100%;
    background-color: var(--text);
    transition: transform 0.6s ease-in-out, top 0.3s ease-in-out, bottom 0.3s ease-in-out;
  }

  &::after {
    bottom: 0;
  }

  &::before {
    top: 0;
  }
}

@include tablet {
  .burger {
    display: block;
    margin: 14px 0 0 0;

    &--active {
      & .burger__line {
        opacity: 0;
      }

      &::before {
        top: 50%;
        transform: rotate(-45deg);
      }

      &::after {
        bottom: auto;
        top: 50%;
        transform: rotate(225deg);
      }
    }
  }
}

@include mobile {
  .burger {
    margin: 0;
    align-self: center;
  }
}

@include tablet {
  .header__nav {
    &.nav {
      position: fixed;
      right: 0;
      top: 0;
      padding: 30px;
      width: 80%;
      overflow-y: auto;
      height: 100vh;
      z-index: 1000;
      background-color: rgba(255, 255, 255, 0.95);

      transform: translateX(100%);
      transition: transform 0.7s ease-in-out;

      @include mobile {
        max-width: 280px;
      }

      &--active {
        transition: transform 0.7s ease-in-out;
        transform: none;
      }

      &__list {
        flex-direction: column;
        align-items: center;
      }

      &__item {
        &:not(:last-child) {
          margin-right: 0;
          margin-bottom: 70px;
        }
      }

      &__link {
        font-size: 25px;

        @include mobile {
          font-size: 22px;
        }
      }
    }
  }

  .header--news__nav {
    &.nav {
      position: fixed;
      right: 0;
      top: 0;
      padding: 30px;
      width: 80%;
      overflow-y: auto;
      height: 100vh;
      z-index: 1000;
      background-color: rgba(255, 255, 255, 0.95);

      transform: translateX(100%);
      transition: transform 0.7s ease-in-out;

      @include mobile {
        max-width: 280px;
      }

      &--active {
        transition: transform 0.7s ease-in-out;
        transform: none;
      }

      &__list {
        flex-direction: column;
        align-items: center;
      }

      &__item {
        &:not(:last-child) {
          margin-right: 0;
          margin-bottom: 70px;
        }
      }

      &__link {
        font-size: 25px;

        @include mobile {
          font-size: 22px;
        }
      }
    }
  }
}