.list-news {
  margin-bottom: var(--margin-desk);

  &__container {
    display: flex;
    justify-content: space-between;
    padding: 0 var(--container-offset-sec);
    max-width: var(--container-width-sec);

    @include big-desktop {
      flex-direction: column;
      align-items: center;
    }
  }

  &__aside {
    width: 30%;

    @include big-desktop {
      width: 100%;
    }
  }

  &__parts {

    @include big-desktop {
      width: 100%;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
    }

    @include tablet {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
    }

    @include small-tablet {
      display: block;
    }
  }

  &__part {
    width: 100%;

    @include big-desktop {
      width: 33%;
    }

    @include tablet {
      width: 45%;
    }

    @include small-tablet {
      width: 100%;
    }
  }

  &__list {
    width: 65%;


    @include big-desktop {
      width: 85%;
    }

    @include tablet {
      width: 100%;
    }
  }

  &__title-main {
    display: inline-block;
    text-align: left;
    margin: 0;
    margin-top: 10px;
    margin-bottom: 25px;
    padding: 0;
    line-height: 35px;
    font-weight: 700;
    font-size: 28px;

    @include mobile {
      font-size: 22px;
    }
  }

  &__title {
    display: inline-block;
    text-align: left;
    margin: 0;
    margin-top: 10px;
    margin-bottom: 10px;
    padding: 0;
    line-height: 35px;
    font-weight: 700;
    font-size: 26px;
  }

  &__image {
    width: 100%;
    height: 350px;
    padding-bottom: 15px;
    padding-bottom: 0;
    display: block;
  }

  &__item {
    margin-bottom: var(--margin-desk);

    @include small-tablet {
      margin-bottom: var(--margin-tablet);
    }

    @include mobile {
      margin-bottom: var(--margin-mobile);
    }
  }



  &__meter {
    display: flex;
    justify-content: space-between;
    margin-bottom: 25px;
  }

  &__meter-item {

    .fa-regular {
      font-size: 25px;
      color: rgb(60, 60, 187);

      @include mobile {
        font-size: 16px;
      }
    }
  }

  &__image-part {
    width: 100%;
    height: 250px;
  }

  &__link {
    transition: color 0.3s;
    color: var(--text);
    position: relative;
    padding-left: 5px;

    &:hover {
      transition: color 0.3s;
      color: var(--main-color);
    }

    &:after {
      position: absolute;
      background-color: var(--red-bil);
      display: block;
      top: 0px;
      left: -7px;
      content: "";
      height: 0%;
      width: 2px;
      -webkit-transition: height .15s ease-in-out;
      -moz--transition: height .15s ease-in-out;
      transition: height .15s ease-in-out;
    }

    &:hover:after,
    &:focus:after {
      height: 100%;
    }
  }

  &__themes {
    padding: 8px 10px;
    border: 1px solid var(--text-grey);
    max-height: 100px;
    overflow: hidden;
    width: 100%;

    @include big-desktop {
      max-width: 40%;
    }

    &:hover {
      overflow-y: scroll;
    }
  }

  &__theme {
    &:not(:last-child) {
      padding-bottom: 15px;
    }
  }

  &__text {
    margin: 0;
    font-size: 16px;
    line-height: 23px;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 6;
    -webkit-box-orient: vertical;
  }

  &__aside-title {
    margin: 0;
    font-size: 16px;
    font-weight: 700;
    line-height: 23px;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 6;
    -webkit-box-orient: vertical;
  }

  &__continue {
    padding-right: 18px;
    transition: color 0.3s;

    &:hover .move-right {
      transition: all 0.2s ease;
      animation-name: move-reverse;
      animation-direction: alternate;
      animation-duration: 0.4s;
      animation-iteration-count: infinite;

    }

    &:hover {
      transition: color 0.3s;
      color: var(--main-color);
    }
  }

  &__arrow {
    position: absolute;
    right: 2px;
  }

  &__continue,
  &__time {
    font-size: 11px;
    color: var(--text);
    opacity: 0.8;
  }

  &__infopanel {
    position: relative;
    margin-top: 32px;
    padding-bottom: 26px;
    @include flex-all-sb;
  }
}

.video-tag::before {
  color: #000;
  bottom: 20px;
  left: 20px;
  position: absolute;
  content: '';
  text-transform: uppercase;
  font-size: 10px;
  font-weight: 700;
  padding: 5px 10px;
  background-color: var(--main-color);
  border-radius: 13px;
}

.albom-tag::before {
  color: #000;
  bottom: 20px;
  left: 20px;
  position: absolute;
  content: '';
  text-transform: uppercase;
  font-size: 10px;
  font-weight: 700;
  padding: 5px 10px;
  background-color: var(--main-color);
  border-radius: 13px;
  z-index: 100;
}

.news-tag::before {
  color: #000;
  bottom: 30px;
  left: 20px;
  position: absolute;
  content: '';
  text-transform: uppercase;
  font-size: 10px;
  font-weight: 700;
  padding: 5px 10px;
  background-color: var(--main-color);
  border-radius: 13px;
  z-index: 100;
}

.meter-item {
  display: flex;
  align-items: center;

  &__icon {
    width: 23%;

    @include mobile {
      width: 21%;
    }
  }

  &__author,
  &__timing,
  &__eying,
  &__restarting {
    margin-left: 10px;
    font-size: 12px;

    @include mobile {
      font-size: 10px;
    }
  }
}

.image-wrapper {
  position: relative;
}