.categories {
  &__container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    gap: 20px;
    margin-bottom: var(--margin-desk);

    @include tablet {
      flex-wrap: wrap;
      margin-bottom: var(--margin-tablet);
    }
  }

  &__item {
    width: 25%;

    @include tablet {
      width: 48%;
    }

    @include small-tablet {
      width: 100%;
    }

    @include mobile {
      height: fit-content;
    }
  }

  &__title {
    border-top: 2px solid var(--text);
    border-bottom: 1px solid var(--text);
    text-align: center;
    font-size: 19px;
    text-transform: uppercase;
    margin: 0;
    padding: 10px 0;
    margin-bottom: 10px;
  }


}

.chempionship {
  &__title {
    width: 100%;
    font-size: 16px;
    padding: 0;
    margin: 0;

    @include big-desktop {
      font-size: 12px;
    }
  }

  &__list {
    @include small-tablet {
      display: flex;
    }

    @include mobile {
      flex-direction: column;
    }
  }

  &__item {
    padding: 10px 16px;
    display: flex;

    @include big-desktop {
      padding: 10px 0px;
    }

    @include small-tablet {
      padding: 10px 2px;
      flex-direction: column;

    }
  }

  &__tag {
    padding: 0;
    margin: 0;
  }

  &__img {
    width: 26%;

    @include big-desktop {
      width: 42%;
    }

    @include small-tablet {
      width: 100%;
      min-height: 80%;
    }
  }

  &__info {
    padding: 0px 0px 0px 10px;
  }

}

.fotos {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;

  &__list {
    display: grid;
    grid-gap: 4px;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, 77px);

    @include tablet {
      grid-template-rows: repeat(3, 120px);

    }
  }

  &__img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__link {
    display: block;
    width: 100%;
    height: 100%;
    position: relative;

    &:hover {
      &::before {
        transition: background-color 0.3s;
        background-color: rgba(0, 0, 0, 0.0);
      }
    }

    &:focus {
      outline: 2px solid var(--main-color);
      outline-offset: 0px;
    }


    &::before {
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: rgba(0, 0, 0, 0.5);
      z-index: 10;
      transition: background-color 0.3s;
    }
  }
}

.recent-news {
  &__item {
    display: flex;

    &:nth-child(odd) {
      @include tablet {
        flex: 1 0 auto;
      }
    }

    &:nth-child(even) {
      @include tablet {
        flex: 0 0 auto;
      }
    }

  }

  &__list {
    @include tablet {
      height: 100%;
      max-height: 305px;
      display: flex;
      justify-content: space-between;
      flex-direction: column;
    }

    @include small-tablet {
      max-height: 158px;
    }

    @media (max-width: (622px)) {
      max-height: 190px;
    }

    @include mobile {
      max-height: 280px;
    }
  }

  &__svg-vk {
    display: inline-block;
    width: 34px;
    height: 34px;
  }

  &__text {
    width: 85%;
    margin: 0;
    margin-left: 8px;
    font-size: 15px;
    line-height: 24px;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 5;
    -webkit-box-orient: vertical;
  }

  &__author {
    font-weight: 700;
  }
}

.streetball {

  &__title {
    width: 100%;
    font-size: 16px;
    padding: 0;
    margin: 0;

    @include big-desktop {
      font-size: 12px;
    }

    @include mobile {
      font-size: 17px;
      text-align: center;
    }
  }

  &__list {
    @include small-tablet {
      display: flex;
    }

    @include mobile {
      flex-direction: column;
    }
  }

  &__item {
    padding: 10px 16px;
    display: flex;

    @include small-tablet {
      padding: 10px 2px;
      flex-direction: column;
    }
  }

  &__tag {
    padding: 0;
    margin: 0;

    @include mobile {
      display: inline;
    }
  }

  &__img {
    width: 26%;

    @include big-desktop {
      width: 38%;
    }

    @include small-tablet {
      width: 100%;
      min-height: 80%;
    }
  }

  &__info {
    padding: 0px 0px 0px 10px;
  }
}