.footer {
  background-color: rgba(0, 0, 0, 0.88);
  padding-bottom: 10px;

  &__container {
    margin: 0 auto;
    padding: 0 var(--container-offset-sec);
    max-width: var(--container-width-sec);
    @include flex-all-center;

    @include small-tablet {
      flex-direction: column;
    }
  }

  &__title {
    text-transform: uppercase;
    font-size: 16px;
    line-height: 23px;

  }

  &__nav {
    padding: 40px 0px;
    width: 20%;

    @include small-tablet {
      width: 100%;
    }

    @include mobile {
      text-align: center;
      padding: 10px 0;
    }
  }

  &__nav-list {
    @include mobile {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
    }
  }

  &__label {
    width: 69px;
  }
}

.about-team {
  padding: 0 50px;


  @include small-tablet {
    padding: 0 4px;
  }

  @include mobile {
    width: 100%;
  }

  &__title {
    font-size: 18px;
    font-weight: 400;
    color: var(--white-text);
    margin: 0;
    text-align: left;
    padding: 0px;
    margin-bottom: 23px;

    @include mobile {
      text-align: center;
      font-size: 12px;
      padding: 0px 0px;
    }
  }

  &__text {
    margin-top: 10px;
    margin-bottom: 10px;
    color: var(--white-text);
    display: inline-block;

    @include mobile {
      text-align: center;
      font-size: 12px;
      padding: 0px 0px;
    }

    &>a>i {
      transition: color 0.3s;

      &:hover {
        transition: color 0.3s;
        color: var(--main-color);
      }
    }


    &>a {
      color: var(--white-text);
      display: inline-block;
      font-weight: 400;
    }

  }

  &__social-list {
    @include flex-v-center;

    @include mobile {
      flex-wrap: wrap;
      margin-bottom: 25px;
      justify-content: space-between;
    }
  }

  &__social-item {
    &:not(:last-child) {
      margin-right: 20px;

    }

    &:first-child {
      @include mobile {
        flex-grow: 1;
        width: 100%;
        text-align: center;
      }
    }


    @include mobile {
      justify-content: center;
    }

  }

  &__svg-vk,
  &__svg-tg,
  &__svg-tw,
  &__svg-ya,
  &__svg-yot {
    display: inline-block;
    width: 34px;
    height: 34px;
    fill: var(--white-text);
    transition: fill 0.3s;
    cursor: pointer;

    &:hover {
      transition: fill 0.3s;
      fill: var(--main-color);
    }
  }


  &__svg-tik {
    display: inline-block;
    width: 34px;
    height: 34px;
    cursor: pointer;

    path {
      transition: stroke 0.3s;
      stroke: var(--white-text);
    }

    &:hover path {
      transition: stroke 0.3s;
      stroke: var(--main-color);
    }
  }

}