.btn {
  padding: 10px 20px;
  font-weight: 700;
  text-transform: uppercase;
  font-size: 18px;
  line-height: 21px;
  border-radius: 5px;
  text-align: center;


  &--fill {
    transition: background-color 0.3s;
    background-color: var(--background-cool);

    &:hover {
      transition: background-color 0.3s;
      background-color: var(--ligth-black);
    }
  }
}