.interactive {
  position: relative;
  overflow: hidden;
  border-radius: 4px;

  &:hover {
    & .overlay-2::before {
      transition: background-color 0.3s;
      background-color: rgba(0, 0, 0, 0.0);
    }
  }

  &__video {
    display: block;
    width: 100%;
    height: 100%;
    background-image: url('../img/alboms-back.jpg');
    background-repeat: no-repeat;
    background-size: cover;
  }

  &__play {
    @include flex-all-center;

    cursor: pointer;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background-color: var(--main-color);
    border-radius: 100%;
    width: 80px;
    height: 80px;
    z-index: 11;
    transition: background-color 0.3s ease-in-out, opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;

    svg {
      fill: var(--white-text);
      width: 26px;
      height: 26px;
    }

    &:focus {
      outline: 2px solid var(--main-color);
      outline-offset: 2px;
    }

    &:hover {
      background-color: var(--parket-color);
    }

    &:active {
      background-color: var(--parket-color);
    }

    &--played {
      opacity: 0;
      visibility: hidden;
    }
  }


  &__muted {
    cursor: pointer;
    position: absolute;
    left: 40px;
    bottom: 40px;
    transform: translate(-50%, -50%);
    background-color: rgba(#000, 0.12);
    border-radius: 100%;
    width: 50px;
    height: 50px;
    z-index: 11;
    transition: background-color 0.3s ease-in-out, opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;

    svg {
      fill: var(--white-text);
      width: 26px;
      height: 26px;
    }

    &:focus {
      outline: 2px solid var(--main-color);
      outline-offset: 2px;
    }

    &:hover {
      background-color: var(--parket-color);
    }

    &:active {
      background-color: var(--parket-color);


      &--played {
        opacity: 0;
        visibility: hidden;
      }
    }
  }
}