.files {
  background-color: var(--white-text);
  box-shadow: var(--text, 0.5) 0px 4px 5px 0px;
  margin-bottom: var(--margin-big-desktop);

  &__title {
    text-transform: uppercase;
    margin: 0;
    padding-top: 21px;
    padding-bottom: 9px;
    font-weight: 400;
    font-size: 32px;
    text-align: center;
  }

  &__list {
    position: relative;
    counter-reset: mycounter;
  }

  &__arrow {
    position: absolute;
    left: 10px;
    top: 21px;
    font-size: 40px;

    &::after {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      font-size: 16px;
      counter-increment: mycounter;
      content: counter(mycounter);
    }
  }

  &__item {
    position: relative;
    padding-left: 50px;
    padding-top: 20px;
    padding-bottom: 20px;

    &:not(:last-child) {
      border-bottom: 1px solid var(--text-grey);
    }
  }

  &__link {
    color: var(--text);
  }

  &__text {
    margin: 0;

    font-size: 17px;
    line-height: 24px;
    font-weight: 400;
    text-align: left;
    color: var(--text);
  }
}
