.main-alboms-swiper {
  width: 100%;
  height: 100%;
}

.foto-alboms {
  padding-top: 20px;
  margin-bottom: var(--margin-desk);

  @include tablet {
    margin-bottom: var(--margin-tablet);
    padding-top: 0px
  }

  &__container {
    position: relative;
    margin: 0 auto;
    padding: 0 var(--container-offset-sec);
    max-width: var(--container-width-sec);
    z-index: 0;
  }

  &__title {
    text-align: center;
    font-size: 35px;
    margin-bottom: 60px;
  }

  &__list {
    z-index: 10;
    display: flex;
    flex-direction: row;
    padding: 0 3px;

    @include tablet {
      justify-content: center;
    }

    @include small-tablet {
      justify-content: flex-start;
    }

    .swiper-slide .foto-alboms__link {
      margin: 4px;
    }
  }

  &__img {
    object-fit: cover;
    width: 100%;
    height: 100%;
  }

  &__link {
    display: block;
    position: relative;
    height: 350px;

    @include tablet {
      min-height: 210px;
    }

    &:focus {
      outline: 2px solid var(--main-color);
      outline-offset: 2px;
    }

    &:hover {
      &::before {
        transition: background-color 0.3s;
        background-color: rgba(0, 0, 0, 0.0);
      }
    }

    &::before {
      content: '';
      position: absolute;
      width: 100%;
      height: 99%;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: rgba(0, 0, 0, 0.3);
      z-index: 10;
      transition: background-color 0.5s;
    }

  }

  &__descr {
    position: relative;
    height: 100%;
    margin-top: 16px;
  }

  &__newstitle {
    margin: 0;
    display: inline-block;
    line-height: 24px;
    font-weight: 700;
    font-size: 16px;
    margin-bottom: 10px;
  }

  &__text {
    margin: 0;
    padding: 0;
    font-size: 16px;
    line-height: 24px;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
  }

  .swiper-button-prev,
  .swiper-button-next {
    color: #000;
    top: 12%;
    width: 24px;
    height: 24px;
    border: 1px solid var(--grey-line);

    &:after {
      font-size: 16px;
      font-weight: 800;
      position: absolute;
    }

    &:before {
      position: relative;
      content: "";
      display: inline-block;
      vertical-align: middle;
      height: 1px;
      background-color: var(--text);
    }
  }

  .swiper-button-prev {
    left: 48%;
    transition: background-color 0.3s;

    @include tablet {
      left: 47%;
    }

    @include small-tablet {
      left: 44%;
    }

    @include mobile {
      left: 43%;
    }

    &:before {
      width: 100px;
      margin-left: -340%;
      left: -23px;
    }

    &:hover {
      transition: background-color 0.3s;
      background-color: var(--main-color);
    }

  }

  .swiper-button-next {
    right: 48%;
    transition: background-color 0.3s;

    @include tablet {
      right: 47%;
    }

    @include small-tablet {
      right: 48%;
    }

    @include mobile {
      right: 44%;
    }

    &:before {
      margin-right: -340%;
      right: -23px;
      width: 100px;
    }

    &:hover {
      transition: background-color 0.3s;
      background-color: var(--main-color);
    }
  }

}