//     sass- -  root
// colors
:root {
  // base
  --font-family: "Exo2", sans-serif;
  --font-family-Montserrat: "MontserratAlternates", sans-serif;
  --content-width: 1410px;
  --content-width-sec: 1180px;
  --content-width-three: 1040px;
  --container-offset: 15px;
  --container-offset-sec: 20px;
  --container-offset-three: 40px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --container-width-sec: calc(var(--content-width-sec) + (var(--container-offset) * 2));
  --margin-big-desktop: 80px;
  --margin-desk: 40px;
  --margin-tablet: 30px;
  --margin-mobile: 15px;
  // colors
  --main-color: #f5a63f;
  --back: #dbdfe3;
  --text: rgb(0, 0, 0);
  --white-text: #fff;
  --red-bil: #750916;
  --parket-color: #027A71;
  --grey-line: #f2f2f2;
  --text-grey: #bcaaaa;
  --background-cool: #000000e0;
  --ligth-black: #272525e0;
  --footer-grey: #2f3234;
}