.featured-videos {

  &__container {
    padding: 0 var(--container-offset-sec);
    max-width: var(--container-width-sec);
    @include flex-all-sb;
    gap: 8px;
    align-items: flex-start;

    @include tablet {
      flex-direction: column-reverse;
      margin-bottom: var(--margin-tablet);
    }
  }

  &__random {
    width: 40%;

    @include tablet {
      width: 100%;
      margin-bottom: 20px;
    }
  }

  &__main {
    margin-top: 13px;
    width: 60%;
    border-top: 2px solid #000;

    @include tablet {
      width: 100%;
    }
  }

  &__text {
    margin: 0;
    font-size: 16px;
    line-height: 23px;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 5;
    -webkit-box-orient: vertical;
  }

  &__item {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;

    &:nth-child(1) {
      margin-top: 20px;
    }
  }

  &__img {
    width: 100%;
    margin-bottom: 20px;
    margin-bottom: 0;
  }

  &__title,
  &__newstitle {
    margin: 0;
    text-align: center;
    text-align: center;
  }

  &__title {
    position: relative;
    text-transform: uppercase;
    padding-top: 4px;
    padding-bottom: 9px;
    font-weight: 400;
    font-size: 32px;
    border-bottom: 1px solid #000;

  }

  &__newstitle {
    position: relative;
    display: inline-block;
    line-height: 35px;
    font-weight: 700;
    font-size: 28px;
    margin-bottom: 35px;

    @include mobile {
      margin-bottom: 10px;
      font-size: 22px;
      line-height: 29px;
    }

    &::before {
      content: '';
      position: absolute;
      top: -20px;
      left: 50%;
      transform: translateX(-50%);
      font-weight: 300;
      font-size: 14px;
    }
  }

  &__continue {
    padding-right: 18px;
    transition: color 0.3s;

    &:hover .move-right {
      transition: all 0.2s ease;
      animation-name: move-reverse;
      animation-direction: alternate;
      animation-duration: 0.4s;
      animation-iteration-count: infinite;

    }

    &:hover {
      transition: color 0.3s;
      color: var(--main-color);
    }
  }

  &__arrow {
    position: absolute;
    right: 2px;
  }

  &__continue,
  &__time {
    font-size: 11px;
    color: var(--text);
    opacity: 0.8;

    &:focus {
      outline: 2px solid var(--main-color);
      outline-offset: 2px;
    }

  }

  &__infopanel {
    position: relative;
    margin-top: 32px;
    padding-bottom: 26px;
    @include flex-all-sb;

    @include mobile {
      margin-top: 28px;
      padding-bottom: 6px;
    }
  }

  &__link {
    position: relative;

    margin-bottom: 15px;
  }

  &__play {
    @include flex-all-center;

    position: absolute;
    left: 50%;
    top: 50%;
    z-index: 10;
    border-radius: 100%;
    width: 40px;
    height: 40px;
    background-color: var(--main-color);
    transform: translate(-50%, -50%);
    transition: background-color 0.3s ease-in-out, opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;

    svg {
      width: 13px;
      height: 13px;

      use {
        fill: #fff;
      }
    }

    &:focus {
      outline: 2px solid var(--main-color);
      outline-offset: 2px;
    }

    &:hover {
      background-color: var(--parket-color);
    }

    &:active {
      background-color: var(--parket-color);
    }

    &--played {
      opacity: 0;
      visibility: hidden;
    }
  }
}

.dif-videos {

  &__random {
    background-color: #fff;
  }

  &__random-title {
    text-align: end;
    font-size: 28px;
    margin: 0px;
    padding-right: 5px;
    position: relative;

    @include mobile {
      font-size: 25px;
    }

    &:before {
      position: absolute;
      content: "";
      display: inline-block;
      width: 100%;
      height: 1px;
      background-color: var(--text);
    }


    &:before {
      width: 75%;
      top: 45%;
      transform: translateY(-50%);
      left: 0px;

      @include tablet {
        left: 110px;
        top: 58%;
      }

      @include mobile {
        width: 73%;
        left: 91px;
      }
    }

    @include tablet {
      text-align: start;
    }
  }

  &__item {
    margin-top: 19px;
    display: flex;
    border-bottom: 1px solid var(--grey-line);
    padding-bottom: 20px;

    @include big-desktop {
      flex-direction: column;
    }

    @include tablet {
      margin-top: 10px;
      padding-bottom: 10px;
    }
  }

  &__play {
    @include flex-all-center;

    position: absolute;
    left: 50%;
    top: 50%;
    z-index: 10;
    border-radius: 100%;
    width: 28px;
    height: 28px;
    background-color: var(--main-color);
    transform: translate(-50%, -50%);
    transition: background-color 0.3s ease-in-out, opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;

    svg {
      width: 10px;
      height: 10px;

      use {
        fill: #fff;
      }
    }

    &:focus {
      outline: 2px solid var(--main-color);
      outline-offset: 2px;
    }

    &:hover {
      background-color: var(--parket-color);
    }

    &:active {
      background-color: var(--parket-color);
    }

    &--played {
      opacity: 0;
      visibility: hidden;
    }
  }

  &__link {
    font-size: 0;
    align-items: flex-start;
    width: 40%;
    position: relative;

    @include big-desktop {
      width: 90%;
    }

    @include tablet {
      width: 100%;
    }

  }

  &__list {
    margin-top: 32px;

    @include tablet {
      display: grid;
      grid-gap: 10px;
      grid-template-columns: repeat(3, 1fr);
      grid-template-rows: auto;
      margin-top: 15px;
    }

    @include mobile {
      grid-template-columns: repeat(2, 1fr);
    }
  }

  &__img {
    width: 100%;
  }

  &__descr {
    width: 60%;
    position: relative;
    padding: 4px 0px 0px 20px;

    @include big-desktop {
      padding: 0;
      text-align: center;
      margin-top: 10px;
      width: 90%;
    }

    @include tablet {
      width: 100%;
    }
  }

  &__newstitle {
    position: relative;
    margin-bottom: 27px;
    font-size: 16px;

    @include tablet {
      margin-bottom: 11px;
    }

    @include mobile {
      font-size: 13px;
    }

    &:after {
      content: '....................';
      bottom: -10px;
      left: 0;
      position: absolute;

      @include big-desktop {
        left: 50%;
        transform: translateX(-50%);
      }
    }
  }

  &__infopanel {
    position: relative;
    margin-top: 22px;
    @include flex-all-sb;
  }

  &__arrow {
    position: absolute;
    right: 2px;
  }

  &__text {
    font-family: var(--font-family-Montserrat);
    overflow: hidden;
    display: -webkit-box;
    margin: 0;
    margin-bottom: 21px;
    font-size: 12px;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
  }

  &__continue {
    padding-right: 18px;
    color: var(--grey-line);
    transition: color 0.3s;


    &:hover .move-right {
      transition: all 0.2s ease;
      animation-name: move-reverse;
      animation-direction: alternate;
      animation-duration: 0.4s;
      animation-iteration-count: infinite;

    }

    &:hover {
      transition: color 0.3s;
      color: var(--main-color);
    }
  }

  &__continue,
  &__time {
    font-size: 11px;
    color: var(--text);
    opacity: 0.8;
  }

}