.our-alboms {
  position: relative;
  @include flex-all-center;
  flex-direction: column;
  background-color: transparent;
  border-radius: 4px;
  height: 450px;

  &__title,
  &__text {
    color: var(--white-text);
    margin: 0;
  }

  &__descr {
    z-index: 100;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    @include big-desktop {
      width: 60%;
    }

    @include tablet {
      width: 70%;
    }

    @include small-tablet {
      width: 92%;
    }

  }

  &__title {
    padding-top: 170px;
    padding-bottom: 10px;

    @include mobile {
      text-align: center;
    }
  }

  &__content {
    z-index: 0;
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__text {
    font-size: 20px;
    padding-bottom: 20px;
    opacity: 0.8;

    @include small-tablet {
      text-align: center;
    }
  }

  &__btn {
    color: var(--main-color);
    margin-bottom: 200px;
  }
}