.header--news {

  &__container {
    margin: 0 auto;
    position: relative;
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
    padding: 0px var(--container-offset-sec);
    max-width: var(--content-width-three);
    margin-top: 20px;

    @include large-desktop {
      max-width: var(--container-width-sec);
    }

    .burger {
      @include mobile {
        width: 31px;
        height: 25px;
      }
    }


    @include tablet {
      flex-direction: row;
      justify-content: space-between;
      align-items: flex-start;
      margin-bottom: var(--margin-tablet);
    }

    @include mobile {
      align-items: center;
      margin-top: 10px;
      margin-bottom: var(--margin-mobile);
    }
  }

  &__logo {
    display: block;
    align-items: center;

    @include for-desktop {
      order: -1;
    }

    @include big-desktop {
      order: -1;
    }

    @include tablet {
      width: 6%;
      padding-bottom: 12px;
      padding-bottom: 0;
      // width: 15%;
      order: 1;
    }

    @include small-tablet {
      width: 10%;
    }
  }

  &__title-two {
    font-size: 24px;
    text-align: center;
    text-transform: uppercase;
    margin: 0px 0px 10px 0px;
  }

  &__social-list {
    flex-grow: 1;
    display: flex;
    align-items: center;
    justify-content: flex-end;

    @include tablet {
      order: 2;
      justify-content: center;
    }

    @include small-tablet {
      order: 2;
      justify-content: center;
    }
  }

  &__social-item {
    &:not(:first-child) {
      margin-left: 20px;
    }
  }

  &__svg-vk,
  &__svg-tg,
  &__svg-tw,
  &__svg-ya,
  &__svg-yot {
    display: inline-block;
    width: 34px;
    height: 34px;
    fill: var(text);
    transition: fill 0.3s;
    cursor: pointer;

    @include tablet {
      width: 28px;
      height: 28px;
    }

    @include mobile {
      width: 24px;
      height: 24px;
    }

    &:hover {
      transition: fill 0.3s;
      fill: var(--main-color);
    }
  }

  &__svg-tik {
    display: inline-block;
    width: 34px;
    height: 34px;
    cursor: pointer;

    path {
      transition: stroke 0.3s;
      stroke: var(--text);
    }

    &:hover path {
      transition: stroke 0.3s;
      stroke: var(--main-color);
    }
  }

  &__title {
    position: relative;

    @include tablet {
      width: 68%;
      text-align: center;
    }


    @include small-tablet {
      // margin-left: 8%;
    }

    @include mobile {
      margin-left: 0;
    }

    @include mini-mobile {
      width: 74%;
    }
  }

  &__nav {
    width: 100%;
    background-color: transparent;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    margin-bottom: 10px;


    // max-width: var(--container-width-sec);
    @include big-desktop {
      width: 99.8%;
      margin-left: 7%;
    }

    @include tablet {
      order: 3;

    }

    @include mobile {
      margin-bottom: 0;
    }

    .nav__list {
      padding: 0;
      justify-content: flex-start;
      width: 100%;
      border: none;
      padding-left: calc((100% - 1170px)/2);

      @include tablet {
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        height: 100%;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
      }
    }

    .nav__item {
      padding: 10px 18px 10px 0px;
      margin-right: 0;
      cursor: pointer;
      transition: color 0.3s;


      &_active a {
        color: var(--main-color);
      }

      &:hover a {
        transition: color 0.3s;
        color: var(--main-color);
      }

      &:before {
        display: none;
        content: "";
        height: 0px;
        width: 0%;

      }
    }

    .nav__link {
      letter-spacing: 0px;
      font-size: 15px;
      color: var(--text);

      @include tablet {
        font-size: 45px;
        font-weight: 500;
        line-height: 19px;
        text-transform: uppercase;
        transition: color .3s;
        cursor: pointer;
      }

      @include tablet {
        font-size: 32px;
      }

      @include mobile {
        font-size: 24px;
      }
    }
  }

  &__text {
    display: inline-block;
    vertical-align: middle;
    padding: 0;
    margin: 0 auto;
    margin-bottom: 10px;
    margin-left: 10px;
    padding-top: 10px;
    text-align: center;
    text-transform: uppercase;
    font-weight: 400;
    font-size: 24px;
    color: var(--main-color);

    @include big-desktop {
      font-size: 24px;
    }

    @include tablet {
      margin-left: 0;
      font-size: 32px;
    }

    @include small-tablet {
      margin-left: 0;
      font-size: 26px;
    }

    @include mobile {
      margin-left: 0;
      font-size: 18px;
    }

    @include mini-mobile {
      font-size: 18px;
    }

    &_lowcase {
      text-transform: initial;

      // @include mobile {
      //   font-size: 26px;
      // }
    }
  }

  &__time {
    width: 85.2%;
    display: block;
    text-align: start;
    font-weight: 500;
    font-size: 11px;
    margin-left: 12px;

    @include tablet {
      display: inline-block;
      text-align: center;
      // width: 62%;
      margin-bottom: 0;
    }

    @include mobile {
      margin-left: 0px;
    }
  }
}

.breadcrumb {
  @include tablet {
    justify-content: flex-start;
  }
}