.overlay {
  &::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 10;
  }
}

.overlay-2 {
  &::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.35);
    transition: background-color 0.3s;
    z-index: 10;
  }
}

.copyright {
  color: var(--white-text);
  @include flex-all-center;
}

.border-line {
  border-bottom: 1px solid var(--grey-line);
}

.scollToTop {
  cursor: pointer;
  position: fixed;
  width: 50px;
  height: 50px;
  bottom: 50px;
  right: 70px;
  font-size: 50px;
  z-index: 100;
  opacity: 0.75;
  transition: opacity 0.3s;

  & svg {
    fill: var(--main-color);
  }

  &.isShowBtn {
    &_hide {
      transition: opacity 0.3s;
      opacity: 0;
    }
  }
}
