.info-block {
  background-color: var(--background-cool);

  &__container {
    padding: 0 var(--container-offset-sec);
    max-width: var(--container-width-sec);
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__list {
    width: 33%;
    padding-top: 25px;
    padding-bottom: 15px;

    @include tablet {
      width: 50%;
    }

    @include small-tablet {
      width: 60%;
    }

    @include mobile {
      width: 70%;
    }

    @include mobile {
      padding-top: 10px;
      padding-bottom: 5px;
      width: 45%;
    }
  }

  &__item {
    padding-left: 10px;
    display: inline;
    margin-bottom: 5px;

    @include mobile {
      padding-left: 5px;
      display: block;
    }
  }

  &__icon-of-list {

    &.fa-display {
      vertical-align: middle;
      color: var(--main-color);
      font-size: 28px;

      @include tablet {
        font-size: 24px;
      }

      @include small-tablet {
        font-size: 20px;
      }

      @include mobile {
        font-size: 16px;
      }
    }
  }

  &__icon {
    text-align: center;
    color: var(--main-color);
    font-size: 12px;

    &.fa-circle-stop {
      color: var(--red-bil);
    }

    &.fa-volume-high {
      color: var(--parket-color);
      transition: color 0.3s;
      cursor: pointer;

      &.change-size {
        transition: all 0.2s ease;
        animation-name: move-fontsize;
        animation-direction: alternate;
        animation-duration: 0.4s;
        animation-iteration-count: infinite;

      }

      &:hover {
        transition: color 0.3s;
        color: var(--main-color);
      }
    }

    &.fa-volume-high.off {
      color: var(--text-grey);
      opacity: 0.7;
    }
  }

  &__search-header {
    overflow: hidden;
    position: relative;
    width: 33px;
    height: 33px;
    margin-right: 1%;
    margin-right: 0%;
    background-color: var(--white-text);
    border-radius: 35px;
    transition: width 0.5s;
    @include flex-all-center;

    &.active {
      width: 360px;
      transition: width 0.5s;

      @include small-tablet {
        width: 305px;
      }

      @include mobile {
        width: 200px;
      }
    }
  }

  &__search {
    outline: 0;
    width: 80%;
    font-size: 18px;
    border: none;
    border-radius: 5px;
    padding: 4px 4px 4px 4px;
    transition: width 0.5s;

    @include mobile {
      font-size: 14px;
      width: 67%;
    }

    // &.active {
    //   width: 80%;
    //   transition: width 0.5s;
    // }

    &-loop {
      color: var(--text);
      cursor: pointer;
      z-index: 10;
      position: absolute;
      top: 0;
      left: 0;
      width: 35px;
      height: 35px;
      right: 20px;
      background-color: var(--white-text);
      border-radius: 40px;
      transition: color 0.3s;
      @include flex-all-center;

      &:hover {
        transition: color 0.3s;
        color: var(--main-color);
      }
    }
  }

  &__clear {
    position: absolute;
    top: 50%;
    right: 15px;
    width: 15px;
    height: 15px;
    cursor: pointer;
    transform: translateY(-50%);
    transition: color 0.3s;

    &:hover::before,
    &:hover::after {
      transition: background-color 0.3s;
      background-color: var(--main-color);
    }



    &::before {
      position: absolute;
      content: '';
      width: 2px;
      height: 15px;
      background-color: var(--text);
      transform: rotate(45deg);
      transition: background-color 0.3s;

    }

    &::after {
      position: absolute;
      content: '';
      width: 2px;
      height: 15px;
      background-color: var(--text);
      transform: rotate(-45deg);
      transition: background-color 0.3s;

    }
  }

  &__text {
    position: relative;
    margin: 0;
    padding-right: 10px;
    display: inline-block;
    text-transform: uppercase;
    font-weight: 600;
    font-size: 12px;
    color: var(--main-color);
    cursor: pointer;
    transition: color 0.3s;

    @include small-tablet {
      font-size: 11px;
    }

    @include mobile {
      font-size: 10px;
    }

    &:hover {
      transition: color 0.3s;
      color: var(--white-text);
    }

    &:first-child {
      padding-top: 10px;
    }
  }

}