.calendar {
  position: relative;
  width: 110px;

  &:focus-visible {
    border: none;
  }

}

.datepicker {
  outline: none;
  z-index: 10;
  content: '';
  width: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  position: absolute;
  font-size: 0;
  border: none;
  background-color: transparent;
}