.aside {

  &__descr {
    position: relative;
    padding: 4px 22px 28px 22px;

    @include mobile {
      padding: 0;
    }
  }

  &__title {
    position: relative;
    margin: 0;
    margin-bottom: 20px;
    font-weight: 600;
    font-size: 28px;

    @include mobile {
      font-size: 25px;
    }
  }

  &__text {
    font-family: var(--font-family-Montserrat);
    overflow: hidden;
    display: -webkit-box;
    margin: 0;
    margin-bottom: 21px;
    font-size: 15px;
    line-height: 23px;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
  }

  &__img {
    // margin-top: 32px;
    width: 35%;
    max-height: 180px;

    @include mobile {
      width: 100%;
    }
  }

  &__list {
    margin-top: 32px;
  }

  &__item {
    margin-top: 19px;
    display: flex;
    border-bottom: 1px solid var(--text-grey);

    @include mobile {
      flex-direction: column;
    }
  }

  &__newstitle {
    margin-bottom: 27px;
    font-size: 18px;

    @include mobile {
      margin-bottom: 13px;
    }

  }

  &__infopanel {
    position: relative;
    margin-top: 22px;
    @include flex-all-sb;
  }

  &__arrow {
    position: absolute;
    right: 2px;
  }

  &__continue {
    padding-right: 18px;
    color: var(--grey-line);
    transition: color 0.3s;


    &:hover .move-right {
      transition: all 0.2s ease;
      animation-name: move-reverse;
      animation-direction: alternate;
      animation-duration: 0.4s;
      animation-iteration-count: infinite;

    }

    &:hover {
      transition: color 0.3s;
      color: var(--main-color);
    }
  }


  &__continue,
  &__time {
    font-size: 11px;
    color: var(--text);
    opacity: 0.8;

    &:focus {
      outline: 2px solid var(--main-color);
      outline-offset: 2px;
    }

  }
}

.volume-icon {
  position: relative;

  &::before {
    top: 6px;
    left: 180px;
    font: var(--fa-font-solid);
    position: absolute;
    content: '\f028';
    color: var(--text);
    font-size: 28px;
  }
}